import React, { Component } from 'react';

//Components
import Header from '../../../../common/Header';
import Footer from '../../../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../../../common/Sections/SpecialistSelectionSection';
import Column from '../elements/Column/index';
import NavLink from '../elements/NavLink/index';
import arrow from '../../../../img/link-arrow.svg';
import { faqData } from '../../../../data/faq_data';

class FaqQuestionsPage extends Component {
  render() {
    return (
        <div>
        <Header />

        <Background>
            <Content> 
                <Navigation>
                    <NavLink path={'/faq'} name={'Служба помощи'} />
                    <Arrow> <img src={arrow} alt='' /> </Arrow>
                    <NavLink path={'/faq/section'} name={'Уход за пациентами'} />
                    <Arrow> <img src={arrow} alt='' /> </Arrow>
                    <NavLink 
                        path={'/faq/section/sub_section'} 
                        name={'Главные вопросы'} 
                        color={'#9b9b9b'} 
                        hide={true} 
                        />
                </Navigation>
                <Column 
                    data={ faqData[0]['subsections'][0] } 
                    width={'100%'} 
                    all={true} 
                    font={''}
                    boldTitle={true}
                    />
            </Content>
        </Background>

        <SpecialistSelectionSection />
        <Footer />
        </div>
    )
  }
}

const Arrow = styled.div`
    height: 100%;
    margin: 0 2.5%;
    img {
        width: 8px;
    }
`
const Navigation = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding-top: 10px;
    margin-bottom: 20px;
    @media(max-width: 768px) {
        margin-bottom: 10px;
    }
`
const Background = styled.div`
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 768px) {
        padding-top: 60px;
    }
`
const Content = styled.div`
    padding-left: 4%;
    padding-right: 4%;
    max-width: 1200px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0 auto;
`

export default FaqQuestionsPage;





