import React, { Component } from 'react';

//Components
import Header from '../../../common/Header';
import Footer from '../../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../../common/Sections/SpecialistSelectionSection';
import Section from './elements/Section/index';
import { faqData } from '../../../data/faq_data';

class FaqPage extends Component {
  render() {
    return (
      <div>
        <Header />

        <Background>
            <TitleWrap>
                <Content>
                    <Title size='2.9' weight='500' >Служба помощи</Title>
                </Content>
            </TitleWrap>

            <Content> 
                {
                    faqData.map( (item, i) => {
                        return (
                            <Section data={item} key={i} />
                        )
                    })
                }
            </Content>
        </Background>

        <SpecialistSelectionSection />
        <Footer />
      </div>
    )
  }
}

// @media(max-width: 480px) {}

const Background = styled.div`
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 768px) {
        padding-top: 50px;
    }
`
const Title = styled.h1`
    font-family: "Alegreya-Sans";
    font-size: ${ props => props.size }em;
    font-weight: ${ props => props.weight };
    margin: 0 0;
    margin-bottom: ${ props => props.bottom || 0 }px;
    height: 100%;
    display: flex;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    @media(max-width: 960px) {
        justify-content: center
    }
`

const Content = styled.div`
    padding-left: 4%;
    padding-right: 4%;
    max-width: 1200px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0 auto;
`

const TitleWrap = styled.div`
    height: 100px;
    width: 100%;
    border-bottom: 2px solid rgba(34,34,34,0.2);
    margin-bottom: 40px;
    @media(max-width: 768px) {
        margin-bottom: 15px;
    }
`


export default FaqPage;





