import React from 'react';
import styled from 'styled-components';
import ItemLink from './ItemLink/index';
import { Link } from 'react-router-dom';

const Column = props => {

    let articles = props.all 
        ? props.data.questions 
        : props.data.questions.slice( 0, ( props.data.howManyShow || 5 ));

    let title = props.another
        ? "Другие статьи из раздела"
        : props.data.subsections_name.title;

    let color = props.another ? '#999' : '#222';

    // to ={ `/faq${props.url}${props.data.subsections_name.url}`
    // path={`/faq${props.url}${props.data.subsections_name.url}`}

    return (
        <Wrap width={props.width || ''}>
            <Title size='1.35' weight='700' color={ color } bold={props.boldTitle+''}>
                <StyledLink to ={ `/faq/section/sub_section` } >
                    { title }
                </StyledLink>
            </Title>
            <ColumnItems bottom={props.all} >
                {
                    articles.map( (item, i) => {
                        return (
                            <ItemLink 
                                key={i}
                                data={item}  
                                path={`/faq/section/sub_section/article`}
                            />
                        )
                    })
                }
                {
                    props.all
                        ? 
                            null 
                        :
                            <StyledLink to ={ `/faq/section/sub_section` } >
                                Еще 
                                <LinkNumber>
                                    {
                                        (props.data.questions.length - +props.data.howManyShow) > 0
                                            ? (props.data.questions.length - +props.data.howManyShow)
                                            : ''
                                    }
                                </LinkNumber>  
                                статьи 
                            </StyledLink>

                }

            </ColumnItems>
        </Wrap>
    )
}

const Wrap = styled.div`
    max-width: ${ props => props.width || '30%' };
    flex: 1 1 280px;
    min-width: 280px;
    @media(max-width: 960px) {
        max-width: ${ props => props.width || '70%' }; 
        width: ${ props => props.width || '70%' }; 
    }
`
const LinkNumber = styled.span`
    padding: 0 5px;
    font-size: 0.8em;
`
const StyledLink = styled(Link)`
    margin-top: 15px;
    color: #ff6053;
    display: block;
    width: 100%;
    font-size: 1em;
    text-decoration: none;
    font-weight: 400;
    min-height: 18px;
    padding-bottom: 12px;
    font-family: "Alegreya-Sans";
    transition: ease 0.3s all;
    &:hover {
        color: #ff6053;
    } 
`
const ColumnItems = styled.div`
    border-bottom: ${ props => props.bottom ? 0 : 2 }px solid rgba(34,34,34,0.7);
    margin-bottom: 30px;
`
const Title = styled.h1`
    font-size: ${ props => props.bold === 'true' ? +props.size + 1.5 : props.size }em;
    font-weight: ${ props => props.weight };
    margin: 0 0;
    margin-bottom: 15px;
    border-bottom: 2px solid rgba(34,34,34,0.65);
    display: flex;
    align-items: center;
    box-sizing: border-box;
    a {
        color: ${ props => props.color};
    }
    @media(max-width: 768px) {
        margin-top: 0
    }
`

export default Column


