import React from 'react';
import Column from '../Column/index';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const Section = props => {
  return (
    <Wrap>
      <Title size='2.1' weight='700' bottom='15'>
        <StyledLink to={'/faq/section'}>{props.data.section_name.title}</StyledLink>
      </Title>
      <Questions>
        {
          props.data.subsections.map((item, i) => {
            return (
              <Column data={item} key={i} url={props.data.section_name.url} all={false} />
            )
          })
        }
      </Questions>
    </Wrap>
  )
}

const StyledLink = styled(Link)`
  display: block;
  width: 100%;
  color: #222;
  font-size: 1em;
  text-decoration: none;
  font-weight: 400;
  min-height: 18px;
  padding-bottom: 12px;
  /* border-bottom: 2px solid rgba(34,34,34,0.2); */
  font-family: "Alegreya-Sans";
  transition: ease 0.3s all;
    &:hover {
        color: #ff6053;;
    } 
`

const Wrap = styled.div`
    margin-bottom: 60px;
    @media(max-width: 768px) {
        margin-bottom: 20px;
    }
`

const Questions = styled.div`
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media(max-width: 960px) {
        flex-direction: column;
        align-items: center
    }
`
const Title = styled.h1`
    font-family: "Alegreya-Sans";
    font-size: ${ props => props.size}em;
    font-weight: ${ props => props.weight};
    margin: 0 0;
    margin-bottom: ${ props => props.bottom || 0}px;
    height: 100%;
    display: flex;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    @media(max-width: 960px) {
        a {
            display: flex;
            justify-content: center;
        }
    }
`

export default Section;





